.nv-main-menu__item_expanded .nv-main-menu__link span:after {
  display: inline-block;
  background-image: url("../img/sprite.svg");
  background-size: 521px 497px;
}

.nv-main-menu {
  flex: 1;
}

.nv-main-menu__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nv-main-menu__item {
  margin: 0 23px 0 0;
}

.nv-main-menu__item:nth-last-child(-n + 1) {
  margin-right: 0;
}

.nv-main-menu__item:before {
  display: none;
}

.nv-main-menu__item.active .nv-main-menu__link {
  color: #AE2121;
}

.nv-main-menu__item_expanded .nv-main-menu__link span {
  padding-right: 16px;
  position: relative;
}

.nv-main-menu__item_expanded .nv-main-menu__link span:after {
  content: '';
  position: absolute;
  top: 7px;
  right: 0;
  width: 14px;
  height: 10px;
  background-position: -276px -306px;
}

.nv-main-menu__item_expanded:hover .nv-main-menu-submenu {
  opacity: 1 !important;
  visibility: visible !important;
}

.nv-main-menu__link {
  border: none;
}

.nv-main-menu-submenu {
  position: absolute;
  top: 100%;
  left: 0;
  transition: opacity 0.25s ease-in-out, visibility 0.25s ease-in-out;
  padding-top: 7px;
}

.nv-main-menu-submenu__inner {
  background-color: #FFF;
  border: 1px solid #EAEAEA;
  width: 685px;
  z-index: 1;
  display: flex;
  padding: 18px;
}

.nv-main-menu-submenu__list {
  margin: 0;
  padding: 0 15px 0 0;
}

.nv-main-menu-submenu__item {
  margin-bottom: 20px;
}

.nv-main-menu-submenu__item:before {
  display: none;
}

.nv-main-menu-submenu__item:nth-last-child(-n + 1) {
  margin-bottom: 0;
}

.nv-main-menu-submenu__item.active .nv-main-menu-submenu__link {
  color: #AE2121;
}

.nv-main-menu-submenu__link {
  display: flex;
  align-items: center;
  color: #616161;
  border: none;
}

.nv-main-menu-submenu__link img {
  margin-right: 10px;
}